import React from 'react'
import { Link } from 'react-router-dom'
import './ApplicationsCard.scss'

const instustriesItems = [
    {
        key: 1,
        icon: 'fas fa-chart-bar',
        title: 'Explorao, Visualizao dos dados e Dashboards',
        text: 'Desenvolvimento de Dashboards. Desenvolvemos uma determinada visualizao de dados que  a representao grfica com as informaes. Ao usar os elementos visuais como tabelas, grficos ou mapas, as ferramentas de visualizao de dados fornecem uma maneira acessvel e simples de obtermos o entendimento do seu negcio em pequenos detalhes. Temos aqui os dados de forma bem evidente.'
    },
    {
        key: 2,
        icon: 'fas fa-chart-pie',
        title: 'Anlise e Minerao de dados',
        text: 'A minerao de dados  o processo de extrair informaes valiosas, padres, conhecimentos e insights a partir de grandes conjuntos de dados (Big Data), muitas vezes usando mtodos da interseo de aprendizado de mquina, estatsticas e sistemas de banco de dados. Podemos eliminar valores duplicados ou redundantes.'
    },
    {
        key: 3,
        icon: 'fas fa-search',
        title: 'Solues ERP (Enterprise Resource Planning)',
        text: 'Realizamos pesquisas de mercado, analisamos o seu negcios e ajudamos no desenvolvimento competitivo atravs dos indicadores. Alm disso, realizamos consultorias considerando os setores que atuamos.'
    },
    {
        key: 4,
        icon: 'fas fa-database',
        title: 'Database management',
        text: 'Profissional para bancos de dados relacional? Ajudamos na manuteno e gerenciamento de Databases e Tables.'
    },
    {
        key: 5,
        icon: 'fas fa-history',
        title: 'Otimizao de Query',
        text: 'Uma consulta ineficiente pode sobrecarregar os recursos do banco de dados de produo e causar desempenho lento ou perda de servio para outros usurios se a consulta contiver erros. Portanto,  importante otimizar suas consultas para um impacto mnimo no desempenho do banco de dados.'
    },
    {
        key: 6,
        icon: 'fas fa-robot',
        title: 'Softwares por inteligncia artificial',
        text: 'Realizamos automaes por RPA (exemplo: solues Microsoft) ou utilizamos determinados mtodos de automao de processos de negcios com base em softwares por inteligncia artificial (IA).'
    },
    {
        key: 7,
        icon: 'fas fa-chart-line',
        title: 'Anlises preditivas',
        text: 'Utilizamos algoritmos de aprendizado de mquina para analisar o comportamento do modelo de negcio para obter uma viso personalizada e diante disso com as mesmas aplicaes de aprendizado de mquina conseguimos gerar uma anlise preditiva, isto , realizao de previses.'
    }
]

const ApplicationsCard = () => (
    <div className='applications'>
        <ul className='solutions__links'>
            <li className='solutions__links__item active'>
                <Link to='/solucoes'>Aplicaes</Link>
            </li>
            <li className='solutions__links__item'>
                <Link to='/solucoes/setores'>Setores</Link>
            </li>
        </ul>
        <div className="row applications__list">
            {instustriesItems.map(item =>
                <div key={item.key} className="applications__list__item">
                    {/* <i className="applications__list__item__icon small material-icons">{item.icon}</i> */}
                    <i className={item.icon}></i>
                    <h3 className='applications__list__item__title'>{item.title}</h3>
                    <p className='applications__list__item__text'>{item.text}</p>
                </div>
            )}
        </div>
    </div>
)


export default ApplicationsCard