import React from 'react'
import './About.scss'
import Logos from '../Logos/Logos'

const About = () => (
  <div className='about'>
    <div className='about__container'>
    <div className='padding'>
      <div className='about__container__info text'>
        <div className='about__container__info__title'>Sobre</div>
        <div className='about__container__info__text'>
          Somos uma equipe multidisciplinar de alta qualidade com pessoas de distintas habilidades e temos parcerias com muita experincia em construo de negcios, anlise de dados, visualizaes, anlise preditiva, planejamento por pesquisa de mercado, entre outros. Contamos com profissionais dispostos a encontrar as melhores solues atravs dos dados. Temos uma viso de como os atuais modelos de negcio funcionam e tambm pensamos por uma cultura data-driven.
          <br></br>
          <br></br>
          No momento ns temos parceiros estratgicos distribudos por regies percorrendo o Brasil.
          <br></br>
          <br></br>
          Buscamos gerar valor atravs de cada detalhe de forma minuciosa para a satisfao do cliente.
          <br></br>
          <br></br>
          Entre em contato conosco e nos conte um pouco sobre o seu problema de negcio e como podemos te ajudar.
        </div>
      </div>
    </div>
    <div className='padding'>
      <div className='about__container__info'>
        <div className='about__container__info__title-logo'>Ns trabalhamos com as ferramentas</div>
        <Logos />
      </div>
    </div>
    </div>
  </div>  
)

export default About